.hero-bg {
  background-color: #424856;
  background-size: cover;
  background-attachment: fixed;
  padding: 1em 0 !important;

  .hero-content {
    padding: 100px 15%
  }
  h1 {
    color: #fff;
  }
}

.rating {
  .star {
    font-size: 24px;
    display: inline-block;
    color: #e3e3e3;
  }
  .star:last-child {
    margin-right: 0;
  }
  .star:before {
    content:'\2605';
  }
  .star.full {
    color: #6faf57;
  }
  .star.half:after {
    content: '\2605';
    color: #6faf57;
    position: absolute;
    margin-left: -20px;
    width: 10px;
    overflow: hidden;
  }
}

blockquote {
  position: relative;
  marign: 0;
  padding: 30px 120px;
  font-family:  Georgia, "Times New Roman", Times, serif;
  font-size: 30px;

  &:before,
  &:after {
    position: absolute;
    width: 60px;
    height: 60px;
    font-weight: bold;
    font-size: 100px;
    color: #eee;
    line-height: 1;
  }
  &:before {
    top: 0;
    left: 0;
    content: "\201C";
  }
  &:after {
    right: 0;
    bottom: 0;
    content: "\201D";
  }
}

#modalBtn {
  cursor: pointer;
}

/* The Modal (background) */
#modalSubs {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
  background-color: #fefefe;
  margin: 15% auto; /* 15% from the top and centered */
  padding: 20px;
  border: 1px solid #888;
  width: 50%; /* Could be more or less, depending on screen size */

  .btn {
    border: 0;
  }
}

/* The Close Button */
.modalClose {
  color: #aaa;
  position: relative;
  top: -20px;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.modalClose:hover,
.modalClose:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.g-recaptcha {
  transform-origin: left top;
  -webkit-transform-origin: left top;
}

.feature-link {

  span.added {
    display: block;
    cursor: pointer;
    position: absolute;
    top: 100%;
    left: 50%;
    padding: 1px 18px;
    margin-left: -25px;
    border-bottom-left-radius: 90px;
    border-bottom-right-radius: 90px;
    height: 25px;
    width: 50px;
    background: #fafafa;
    z-index: 1;

    &:after {
      content: '\f078';
      font: normal normal normal 14px/1 FontAwesome;
      vertical-align: top;
    }
    &.collapsed:after {
      content: '\f077';
      font: normal normal normal 14px/1 FontAwesome;
      vertical-align: top;
    }
  }
}

.featurette {
  display: none;
}

.fade {
  opacity: 0;
  -webkit-transition: opacity 0.15s linear;
  -o-transition: opacity 0.15s linear;
  transition: opacity 0.15s linear;
}
.fade.in {
  opacity: 1;
}
.collapse {
  display: none;
  visibility: hidden;
}
.collapse.in {
  display: block;
  visibility: visible;
}
tr.collapse.in {
  display: table-row;
}
tbody.collapse.in {
  display: table-row-group;
}
.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  -webkit-transition-property: height, visibility !important;
  transition-property: height, visibility !important;
  -webkit-transition-duration: 0.35s !important;
  transition-duration: 0.35s !important;
  -webkit-transition-timing-function: ease !important;
  transition-timing-function: ease !important;
}

